
.search-input {
  padding: 10px 10px;
  height: 52px;
  position: relative;
  

}

.search-input::before {
  content: url('data:image/png;base64,');
  display: block;
  position: absolute;
  width: 25px;
  z-index: 3;
  height: 25px;
  font-size: 30px;
  top: 11px;
  left: 22px;
  line-height: 32px;
  opacity: 0.6;
}

.search-input > input {
  width: 98%;
  font-size: 18px;
  border: 2px solid #9dadc5;
  border-radius: 12px;
  -webkit-box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
          box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 15px;

  line-height: 22px;
  padding: 5px 10px 5px 25px;
  height: 42px;
  position: relative;
}

.search-input > input:focus {
  outline: none;
}

::-webkit-input-placeholder { /* Chrome/Opera/Safari */
    color: #303133;
  }
  ::-moz-placeholder { /* Firefox 19+ */
    color: #303133;
  }
  :-ms-input-placeholder { /* IE 10+ */
    color: #303133;
  }
  :-moz-placeholder { /* Firefox 18- */
    color: #303133;
  }


